const sin = Math.sin
const cos = Math.cos
const atan = Math.atan
const atan2 = Math.atan2
const pi = Math.PI

export class Motion {
    constructor() {
        console.log('Welcome...')
        window.game = this
    }
    
    arc = (x, y, r, fill) => {
        // this.ctx.beginPath()
        // this.ctx.arc(x, y, r, 0, 2 * Math.PI)
        // if (fill) {
        //     this.ctx.fill()
        // }
        // this.ctx.stroke()
        
    }
    
    xFunc = (time) => eval(this.xInput) * 120 // Math.sin((this.time) / 30) * 120
    yFunc = (time) => eval(this.yInput) * 120 //Math.cos((this.time) / 20) * 120
    
    setCanvas = (currentCanvas) => {
        this.canvas = currentCanvas.current
        // console.log(currentCanvas)
        this.ctx = this.canvas.getContext('2d')
        this.ctx.fillRect(0,0,this.canvas.width, this.canvas.height)
        // this.loop()
    }
    
    
    
    canvas_arrow = (context, fromx, fromy, tox, toy) => {
        context.beginPath()
        var headlen = 10; // length of head in pixels
        var dx = tox - fromx;
        var dy = toy - fromy;
        var angle = Math.atan2(dy, dx);
        context.moveTo(fromx, fromy);
        context.lineTo(tox, toy);
        context.lineTo(tox - headlen * Math.cos(angle - Math.PI / 6), toy - headlen * Math.sin(angle - Math.PI / 6));
        context.moveTo(tox, toy);
        context.lineTo(tox - headlen * Math.cos(angle + Math.PI / 6), toy - headlen * Math.sin(angle + Math.PI / 6));
        context.stroke()
    }


    
    ld = {};

    loop = () => {
        const ctx = this.ctx;
        const canvas = this.canvas
        if (this.path.length > 1000) this.path.shift()
        // if (this.xFunc(0) === this.x && this.yFunc(0) === this.y && this.time !== 0) this.pathVisible = false
        ctx.fillStyle = 'black';
        ctx.fillRect(0, 0, canvas.width, canvas.height)
        ctx.fillStyle = "white"
        ctx.strokeStyle = "white"
        // ctx.beginPath();
        
        
        ctx.translate(canvas.width / 2, canvas.height / 2);
        //izdmler
        // this.arc(Math.sin(this.time / 30) * 120, Math.cos(this.time / 30) * 120,5,false)
        // this.arc(Math.sin(this.time / 20) * 120, Math.cos(this.time / 20) * 120,5,false)
        // this.arc(0,0, 120);
        
        
        // ctx.fill()
        this.path.push({ x: this.x, y: this.y });
        
        this.path.forEach((p, i) => {

            ctx.beginPath()
            ctx.stroke();
            ctx.fill();
            if (i > 1 && this.pathVisible) {


                ctx.moveTo(p.x, p.y);
                ctx.lineTo(this.ld.x, this.ld.y);
                ctx.stroke();
                
            }
            
            this.ld.x = p.x
            this.ld.y = p.y

            // Draw the Path
            
        })

        
        if (this.vectors) {
            ctx.strokeStyle = "red"
            const len = Math.sqrt(Math.pow(this.vx,2) + Math.pow(this.vy,2)) * 20 
            const a = Math.atan2(this.vy , this.vx)
            ctx.translate(this.x, this.y)
            ctx.rotate(a)
            this.canvas_arrow(ctx, 0, 0, len, 0)
            ctx.rotate(-a)
            ctx.translate(-this.x, -this.y)
            // ctx.stroke();
        }
        
        ctx.beginPath()
        ctx.arc(this.x, this.y, 5, 0, 2 * Math.PI);
        ctx.fill()
        
        ctx.beginPath()
        ctx.arc(0, 0, 2*0, 0, 2 * Math.PI);
        ctx.fill()
        

        // console.log(lena)
        this.cx = this.x
        this.cy = this.y
        this.cvx = this.vx
        this.cvy = this.vy
        try {
            this.x = this.xFunc()
            this.y = this.yFunc()
            
        } catch (error) {
            if(this.reportError) this.reportError(error)
            this.running = false
            
            
        }
        this.vx = this.x - this.cx
        this.vy = this.y - this.cy
        this.ax = this.vx - this.cvx
        this.ay = this.vy - this.cvy
        
        if(this.vectors){
            const aa = Math.atan2(this.ay, this.ax) 
            const lena = Math.sqrt(Math.pow(this.ax, 2) + Math.pow(this.ay, 2)) * 600
            ctx.strokeStyle="blue"
            ctx.translate(this.x,this.y)
            this.ctx.rotate(aa)
            ctx.beginPath()
            this.canvas_arrow(this.ctx, 0, 0,  lena, 0)
            ctx.stroke()
            
            ctx.rotate(-aa)
            ctx.translate(-this.x,-this.y)

        }
        ctx.translate(-canvas.width / 2, -canvas.height / 2)
        this.time++

    }
    
    start = (currentID, setCurrentID, method,vectors,path,func1,func2,reportError) => {
        // return
        if (currentID) clearInterval(currentID);
        this.cx = 0
        this.cy = 0
        this.ax = 0
        this.ay = 0
        this.vx = 0
        this.vy = 0
        this.xInput = func1.replaceAll('x','this.time/30')
        this.yInput = func2.replaceAll('x','this.time/30')

        this.pureX = func1
        this.pureY = func2

        this.reportError = reportError
        
        
        if (currentID) {
            clearInterval(currentID)
        }
        const nowID = setInterval(() => {
            if(this.running) this.loop()
        }, 1000 / 60)
        if(setCurrentID) setCurrentID(nowID)
        this.running = true
        this.sid = nowID
        
        
        
        this.x = 0
        this.y = 0
        this.time = 0
        this.path = []
        window.removePath = () => this.path = []



        this.ctx.lineWidth = 2

        this.method = method
        this.vectors = vectors
        this.pathVisible = path
        window.removePath()
        window.restart = this.restart
    }


    restart = () => {
        console.log('restart')
        clearInterval(this.sid)
        // this.start(null , null,  this.method , this.vectors, this.pathVisible)
    }
    
    showVectors = (value) => {
        if(value ) this.vectors = true
        else this.vectors = false
        // this.vectors = this.vectors ? false : true
    }
    showPath = (value) => {
        if(value ) this.pathVisible = true
        else this.pathVisible = false

        // this.pathVisible = this.pathVisible ? false : true
    }
    
    
    
    
    
    
}