import React from 'react';
import apps from '../apps.json'
import { Link } from 'react-router-dom'


const Apps = () => {
    return (
        <div>
            <h3><u>SIMULATIONS</u></h3>
            {apps.map((app, index) =>  {
                return (
                    <Link key={index} to={`/apps/${app.component}`} id="app-view">
                        {/* <img src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSB6sIbBTPxuop0yMpffyW1oXVRXbtdGNEeSA&usqp=CAU"/> */}
                        <img src="/harmonic.png"/>
                        <div id="app-view-name">{app.name}</div>
                    </Link>
                )
            })}
        </div>
    );
}

export default Apps;
